"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { useToast } from '@/hooks/use-toast'
import { 
  Unlock, 
  Shield, 
  Eye, 
  EyeOff, 
  Key, 
  Loader2,
  AlertCircle
} from 'lucide-react'
import { saveAs } from 'file-saver'
import { saveGeneratedFile, generateFileId } from '@/lib/file-storage'
import { formatBytes } from '@/lib/utils/file-utils'
import { EncryptionService } from '@/lib/services/encryption-service'
import { FileUpload } from '@/components/ui/file-upload'
import { ProcessingResults, type ProcessingResult } from '@/components/ui/processing-results'
import { APP_CONFIG } from '@/lib/config/app-config'
import { isPDFFile } from '@/lib/utils/file-utils'

export function DecryptPDF() {
  const [results, setResults] = useState<ProcessingResult[]>([])
  const [isProcessing, setIsProcessing] = useState(false)
  const [password, setPassword] = useState('')
  const [showPassword, setShowPassword] = useState(false)
  const { toast } = useToast()

  const handleFilesSelected = (files: File[]) => {
    const newResults: ProcessingResult[] = files.map((file, index) => ({
      id: `decrypt-${Date.now()}-${index}`,
      originalFile: file,
      status: 'pending' as const
    }))

    setResults(prev => [...prev, ...newResults])
  }

  const handleDecrypt = async () => {
    if (!password.trim()) {
      toast({
        title: "Error",
        description: "Por favor ingresa la contraseña",
        variant: "destructive",
      })
      return
    }

    const pendingResults = results.filter(r => r.status === 'pending')
    if (pendingResults.length === 0) return

    setIsProcessing(true)

    for (const result of pendingResults) {
      try {
        // Update status to processing
        setResults(prev => prev.map(r => 
          r.id === result.id ? { ...r, status: 'processing' as const } : r
        ))

        // Check if PDF is encrypted first
        const isEncrypted = await EncryptionService.isPDFEncrypted(result.originalFile)
        if (!isEncrypted) {
          throw new Error('El PDF no está encriptado')
        }

        // Decrypt the PDF
        const decryptResult = await EncryptionService.decryptPDF(result.originalFile, password)
        
        // Save the decrypted file
        const fileId = generateFileId()
        
        // Update result with success
        setResults(prev => prev.map(r => 
          r.id === result.id ? {
            ...r,
            status: 'completed' as const,
            result: {
              blob: decryptResult.blob,
              filename: decryptResult.fileName,
              size: decryptResult.blob.size
            }
          } : r
        ))

        toast({
          title: "Éxito",
          description: `${result.originalFile.name} desencriptado correctamente`,
        })

      } catch (error) {
        console.error('Error decrypting PDF:', error)
        
        let errorMessage = 'Error al desencriptar el archivo'
        if (error instanceof Error) {
          if (error.message.includes('Contraseña incorrecta')) {
            errorMessage = 'Contraseña incorrecta'
          } else if (error.message.includes('no está encriptado')) {
            errorMessage = 'El PDF no está encriptado'
          }
        }

        setResults(prev => prev.map(r => 
          r.id === result.id ? {
            ...r,
            status: 'error' as const,
            error: errorMessage
          } : r
        ))

        toast({
          title: "Error",
          description: `${result.originalFile.name}: ${errorMessage}`,
          variant: "destructive",
        })
      }
    }

    setIsProcessing(false)
  }

  const handleDownload = (result: ProcessingResult) => {
    if (result.result?.blob && result.result?.filename) {
      saveAs(result.result.blob, result.result.filename)
    }
  }

  const handleDownloadAll = () => {
    const completedResults = results.filter(r => r.status === 'completed' && r.result?.blob)
    completedResults.forEach(result => {
      if (result.result?.blob && result.result?.filename) {
        saveAs(result.result.blob, result.result.filename)
      }
    })
  }

  const handleRetry = (result: ProcessingResult) => {
    setResults(prev => prev.map(r => 
      r.id === result.id ? { ...r, status: 'pending' as const, error: undefined } : r
    ))
  }

  const clearResults = () => {
    setResults([])
    setPassword('')
  }

  const pendingCount = results.filter(r => r.status === 'pending').length
  const completedCount = results.filter(r => r.status === 'completed').length

  return (
    <div className="space-y-6">
      {/* Subida de archivos */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Unlock className="w-5 h-5" />
            Desencriptar PDF
          </CardTitle>
        </CardHeader>
        <CardContent>
          <FileUpload
            title="Seleccionar archivos PDF"
            description="Selecciona archivos PDF encriptados para desencriptar"
            allowedTypes={['application/pdf']}
            maxFiles={10}
            maxSize={50 * 1024 * 1024} // 50MB
            onFilesSelected={handleFilesSelected}
            disabled={isProcessing}
          />
        </CardContent>
      </Card>

      {/* Configuración de contraseña */}
      {results.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Key className="w-5 h-5" />
              Configuración de desencriptación
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="password">Contraseña del PDF</Label>
              <div className="relative">
                <Input
                  id="password"
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="Ingresa la contraseña del PDF"
                  disabled={isProcessing}
                  className="pr-10"
                />
                <Button
                  type="button"
                  variant="ghost"
                  size="sm"
                  className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                  onClick={() => setShowPassword(!showPassword)}
                  disabled={isProcessing}
                >
                  {showPassword ? (
                    <EyeOff className="h-4 w-4" />
                  ) : (
                    <Eye className="h-4 w-4" />
                  )}
                </Button>
              </div>
            </div>

            <div className="flex items-center gap-2 p-3 bg-blue-50 rounded-lg">
              <AlertCircle className="w-4 h-4 text-blue-600" />
              <p className="text-sm text-blue-800">
                Ingresa la contraseña que se usó para encriptar el PDF
              </p>
            </div>

            <div className="flex gap-2">
              <Button
                onClick={handleDecrypt}
                disabled={isProcessing || pendingCount === 0 || !password.trim()}
                className="flex-1"
              >
                {isProcessing ? (
                  <>
                    <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                    Desencriptando...
                  </>
                ) : (
                  <>
                    <Unlock className="w-4 h-4 mr-2" />
                    Desencriptar {pendingCount} archivo{pendingCount !== 1 ? 's' : ''}
                  </>
                )}
              </Button>

              {results.length > 0 && (
                <Button
                  variant="outline"
                  onClick={clearResults}
                  disabled={isProcessing}
                >
                  Limpiar
                </Button>
              )}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Resultados */}
      <ProcessingResults
        results={results}
        title="Resultados de desencriptación"
        onDownload={handleDownload}
        onDownloadAll={handleDownloadAll}
        onRetry={handleRetry}
        showDownloadAll={completedCount > 0}
      />
    </div>
  )
}